.MainHeader {
  width: 100%;
  height: 80px;
  top: 0;
  left: 0;
  position: fixed;
  background-color: rgb(12, 12, 17);
  box-shadow: 0px 0px 8px #1e1c25;
  box-sizing: border-box;
  text-align: center;
  font-size: 35px;
  z-index: 100;
}

.Title {
  text-align: center;
  margin: 10px 10px;
  padding: -10px;
}
@media (max-width: 500px) {
  .MainHeader {
    width: 100%;
    height: 80px;
    top: 0;
    left: 0;
    position: fixed;
    background-color: rgb(12, 12, 17);
    box-shadow: 0px 0px 8px #1e1c25;
    box-sizing: border-box;
    text-align: center;
    font-size: 35px;
    z-index: 100;
  }
}
