.main {
  display: flex;
  width: auto;
}

.main p {
  padding: 10px;
}

.TypeAhead {
  width: 600px;
  color: red;
}

input[type="text"],
textarea {
  color: white;
  background-color: black;
}
input[type="text"]:focus {
  background-color: black;
  color: white;
}

@media (max-width: 500px) {
  .TypeAhead {
    width: 200px;
  }
}
